"use client";

import { useState } from "react";
import { useQuery } from "@tanstack/react-query";
import { getActiveOffers, getOffer } from "@/lib/api";
import { Offer } from "@/lib/types";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Search, Filter, ArrowRight, Clock, User } from "lucide-react";
import { formatDistanceToNow } from "date-fns";
import { useWalletAuth } from "@/hooks/use-wallet-auth";
import { toast } from "sonner";

type AssetType = "all" | "sol" | "spl" | "nft";

const ASSET_TYPES: { value: AssetType; label: string }[] = [
  { value: "all", label: "All Assets" },
  { value: "sol", label: "SOL" },
  { value: "spl", label: "SPL Tokens" },
  { value: "nft", label: "NFTs" },
];

// Known mint addresses for filtering
const SOL_MINT = "So11111111111111111111111111111111111111112";
const KNOWN_SPL_TOKENS = [
  "EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v", // USDC
  "Es9vMFrzaCERmJfrF4H2FYD4KCoNkY11McCe8BenwNYB", // USDT
];

export default function MarketplacePage() {
  const [searchQuery, setSearchQuery] = useState("");
  const [assetTypeFilter, setAssetTypeFilter] = useState<AssetType>("all");
  const [selectedOffer, setSelectedOffer] = useState<Offer | null>(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const { walletAddress, isAuthenticated } = useWalletAuth();

  const { data: offers, isLoading } = useQuery({
    queryKey: ["marketplace-offers"],
    queryFn: getActiveOffers,
  });

  // Filter offers based on search and asset type
  const filteredOffers = offers?.filter((offer) => {
    // Search filter
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      if (
        !offer.seller.toLowerCase().includes(query) &&
        !offer.token_mint_a.toLowerCase().includes(query) &&
        !offer.token_mint_b.toLowerCase().includes(query)
      ) {
        return false;
      }
    }

    // Asset type filter
    if (assetTypeFilter !== "all") {
      const mintA = offer.token_mint_a;
      const mintB = offer.token_mint_b;

      switch (assetTypeFilter) {
        case "sol":
          if (mintA !== SOL_MINT && mintB !== SOL_MINT) return false;
          break;
        case "spl":
          if (
            !KNOWN_SPL_TOKENS.includes(mintA) &&
            !KNOWN_SPL_TOKENS.includes(mintB)
          ) {
            return false;
          }
          break;
        case "nft":
          // NFTs typically have amount of 1
          if (offer.token_amount_a !== "1" && offer.token_amount_b !== "1") {
            return false;
          }
          break;
      }
    }

    return true;
  });

  const handleOfferClick = (offer: Offer) => {
    setSelectedOffer(offer);
    setIsDialogOpen(true);
  };

  const handleAcceptOffer = async () => {
    if (!selectedOffer || !walletAddress) return;
    
    try {
      toast.loading("Accepting offer...", { id: "accept-offer" });
      
      // TODO: Implement accept offer logic with smart contract interaction
      // This would involve:
      // 1. Creating a proposal transaction
      // 2. Signing with wallet
      // 3. Sending to blockchain
      // 4. Updating backend
      
      await new Promise(resolve => setTimeout(resolve, 2000)); // Simulated delay
      
      toast.success("Offer accepted successfully!", { id: "accept-offer" });
      setIsDialogOpen(false);
    } catch (error) {
      console.error("Error accepting offer:", error);
      toast.error("Failed to accept offer. Please try again.", { id: "accept-offer" });
    }
  };

  const canAcceptOffer = (offer: Offer | null) => {
    if (!offer || !walletAddress || !isAuthenticated) return false;
    return offer.seller.toLowerCase() !== walletAddress.toLowerCase();
  };

  const formatTokenDisplay = (mint: string) => {
    if (mint === SOL_MINT) return "SOL";
    if (KNOWN_SPL_TOKENS.includes(mint)) {
      // Add token symbol mapping if needed
      if (mint === "EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v")
        return "USDC";
      if (mint === "Es9vMFrzaCERmJfrF4H2FYD4KCoNkY11McCe8BenwNYB")
        return "USDT";
    }
    return `${mint.slice(0, 4)}...${mint.slice(-4)}`;
  };

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">Marketplace</h1>
        <p className="text-muted-foreground">
          Browse and accept active swap offers from the community
        </p>
      </div>

      {/* Filters */}
      <Card className="p-6 backdrop-blur-sm bg-background/50 border-border/50">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="relative flex-1">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="Search by seller address or token mint..."
              className="pl-9"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>
          <div className="flex items-center gap-2">
            <Filter className="h-4 w-4 text-muted-foreground" />
            <Select
              value={assetTypeFilter}
              onValueChange={(value) => setAssetTypeFilter(value as AssetType)}
            >
              <SelectTrigger className="w-[180px]">
                <SelectValue placeholder="Filter by type" />
              </SelectTrigger>
              <SelectContent>
                {ASSET_TYPES.map((type) => (
                  <SelectItem key={type.value} value={type.value}>
                    {type.label}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>
      </Card>

      {/* Offers Grid */}
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-medium">
            {filteredOffers?.length || 0} Active Offers
          </h2>
        </div>

        {isLoading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[...Array(6)].map((_, i) => (
              <Card
                key={i}
                className="p-6 space-y-4 backdrop-blur-sm bg-background/50 border-border/50"
              >
                <div className="h-6 w-3/4 bg-muted/50 rounded animate-pulse" />
                <div className="h-4 w-1/2 bg-muted/50 rounded animate-pulse" />
                <div className="h-16 bg-muted/50 rounded animate-pulse" />
                <div className="h-10 bg-muted/50 rounded animate-pulse" />
              </Card>
            ))}
          </div>
        ) : filteredOffers?.length === 0 ? (
          <Card className="p-12 text-center backdrop-blur-sm bg-background/50 border-border/50">
            <p className="text-muted-foreground">
              No offers found matching your filters.
            </p>
          </Card>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredOffers?.map((offer) => (
              <Card
                key={offer.id}
                className="p-6 space-y-4 backdrop-blur-sm bg-background/50 border-border/50 hover:border-primary/50 transition-all cursor-pointer group"
                onClick={() => handleOfferClick(offer)}
              >
                {/* Seller Info */}
                <div className="flex items-center gap-2">
                  <User className="h-4 w-4 text-muted-foreground" />
                  <span className="text-xs font-mono text-muted-foreground">
                    {offer.seller.slice(0, 4)}...{offer.seller.slice(-4)}
                  </span>
                  <Badge
                    variant={offer.allow_alternatives ? "secondary" : "outline"}
                    className="ml-auto text-[10px]"
                  >
                    {offer.allow_alternatives ? "Negotiable" : "Fixed"}
                  </Badge>
                </div>

                {/* Swap Details */}
                <div className="space-y-2">
                  <div className="flex items-center justify-between p-3 rounded-lg bg-muted/30">
                    <div className="space-y-1">
                      <p className="text-xs text-muted-foreground">Selling</p>
                      <p className="text-lg font-semibold">
                        {offer.token_amount_a}
                      </p>
                      <p className="text-xs font-mono text-muted-foreground">
                        {formatTokenDisplay(offer.token_mint_a)}
                      </p>
                    </div>
                    <ArrowRight className="h-5 w-5 text-primary" />
                    <div className="space-y-1 text-right">
                      <p className="text-xs text-muted-foreground">For</p>
                      <p className="text-lg font-semibold">
                        {offer.token_amount_b}
                      </p>
                      <p className="text-xs font-mono text-muted-foreground">
                        {formatTokenDisplay(offer.token_mint_b)}
                      </p>
                    </div>
                  </div>
                </div>

                {/* Time and Action */}
                <div className="flex items-center justify-between pt-2">
                  <div className="flex items-center gap-1 text-xs text-muted-foreground">
                    <Clock className="h-3 w-3" />
                    {formatDistanceToNow(new Date(offer.created_at), {
                      addSuffix: true,
                    })}
                  </div>
                  <Button
                    size="sm"
                    className="group-hover:bg-primary group-hover:text-primary-foreground transition-colors"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleOfferClick(offer);
                    }}
                  >
                    View Details
                  </Button>
                </div>
              </Card>
            ))}
          </div>
        )}
      </div>

      {/* Offer Details Dialog */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="max-w-2xl">
          <DialogHeader>
            <DialogTitle>Offer Details</DialogTitle>
            <DialogDescription>
              Review the offer details before accepting
            </DialogDescription>
          </DialogHeader>

          {selectedOffer && (
            <div className="space-y-6 py-4">
              {/* Seller Info */}
              <div className="space-y-2">
                <h4 className="text-sm font-medium text-muted-foreground">
                  Seller
                </h4>
                <div className="flex items-center gap-2">
                  <code className="text-sm px-3 py-1 bg-muted rounded font-mono">
                    {selectedOffer.seller}
                  </code>
                  <Badge
                    variant={
                      selectedOffer.allow_alternatives ? "secondary" : "outline"
                    }
                  >
                    {selectedOffer.allow_alternatives
                      ? "Accepts Alternatives"
                      : "Fixed Offer"}
                  </Badge>
                </div>
              </div>

              {/* Swap Details */}
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2 p-4 rounded-lg bg-muted/30 border border-border/50">
                  <h4 className="text-sm font-medium text-muted-foreground">
                    Seller Offers
                  </h4>
                  <p className="text-2xl font-bold">
                    {selectedOffer.token_amount_a}
                  </p>
                  <div className="space-y-1">
                    <p className="text-xs text-muted-foreground">Token Mint</p>
                    <code className="text-xs px-2 py-1 bg-background rounded font-mono block break-all">
                      {selectedOffer.token_mint_a}
                    </code>
                  </div>
                </div>

                <div className="space-y-2 p-4 rounded-lg bg-primary/10 border border-primary/30">
                  <h4 className="text-sm font-medium text-muted-foreground">
                    Seller Requests
                  </h4>
                  <p className="text-2xl font-bold">
                    {selectedOffer.token_amount_b}
                  </p>
                  <div className="space-y-1">
                    <p className="text-xs text-muted-foreground">Token Mint</p>
                    <code className="text-xs px-2 py-1 bg-background rounded font-mono block break-all">
                      {selectedOffer.token_mint_b}
                    </code>
                  </div>
                </div>
              </div>

              {/* Additional Info */}
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-muted-foreground">Offer ID:</span>
                  <code className="ml-2 text-xs">
                    {selectedOffer.offer_id.slice(0, 8)}...
                  </code>
                </div>
                <div>
                  <span className="text-muted-foreground">Created:</span>
                  <span className="ml-2">
                    {formatDistanceToNow(new Date(selectedOffer.created_at), {
                      addSuffix: true,
                    })}
                  </span>
                </div>
              </div>

              {/* Warning for own offer */}
              {walletAddress &&
                selectedOffer.seller.toLowerCase() ===
                  walletAddress.toLowerCase() && (
                  <div className="p-4 rounded-lg bg-yellow-500/10 border border-yellow-500/30">
                    <p className="text-sm text-yellow-400">
                      This is your own offer. You cannot accept it.
                    </p>
                  </div>
                )}

              {/* Warning if not authenticated */}
              {!isAuthenticated && (
                <div className="p-4 rounded-lg bg-yellow-500/10 border border-yellow-500/30">
                  <p className="text-sm text-yellow-400">
                    Please connect your wallet to accept this offer.
                  </p>
                </div>
              )}
            </div>
          )}

          <DialogFooter>
            <Button
              variant="outline"
              onClick={() => setIsDialogOpen(false)}
            >
              Close
            </Button>
            <Button
              onClick={handleAcceptOffer}
              disabled={!canAcceptOffer(selectedOffer)}
              className="gap-2"
            >
              Accept Offer
              <ArrowRight className="h-4 w-4" />
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}

